.flavour-card {
  width: 22%;
  height: auto;
  padding: 15px; /* Add padding to create space between cards */
  box-sizing: border-box;
  text-decoration: none;
  overflow: hidden;
  margin-bottom: 20px;
  border-radius: 10px;

  /* Include padding in width calculation */
}

.flavour-card:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.card-image {
  overflow: hidden;
  border-radius: 10px;

}

.flavour-card img {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.flavour-card:hover img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.flavour-title {
  text-align: center;
  margin-top: 15px;
  margin-bottom: 20px;
  font-weight: bold;
  color: #3d246c;
  -webkit-text-decoration-style: none;
          text-decoration-style: none;
  font-size: 25px;
  text-decoration: none;
